@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap);
:root{
  --primary-color : #0B3954;
  --darker-primary : #092f44;
  --secondary-color : #BFD7EA;
  --darker-secondary : darkblue;
  --white : rgb(228, 228, 228);
  --lightgray : rgb(225 225 225);
}

*, html {
  margin: 0 !important;
}

html, body, #root{
  width: 100%;
  min-height: 100vh;
}

h1, h2, h3, h4, h5, h6, p, span, select, div, footer, header, section, nav{
  cursor: default;
}

body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(228, 228, 228);
  background-color: var(--white);
}

nav > *, footer > *, section{
  max-width: 1400px!important;
  width: 100%;
  margin: auto!important;
  padding: 0 30px;
}

/* Fallback for btn, select... */
button{
  color: rgb(228, 228, 228);
  color: var(--white);
  width: 100%;
  max-width: 250px;
  height: 50px;
  border-radius: 10px;
  border: 0;
  font-size: 14pt;
  background-color: #0B3954;
  background-color: var(--primary-color);
  transition: all 0.1s;
}

  button:hover{
    background-color: #092f44;
    background-color: var(--darker-primary);
    transition: all 0.1s;
  }

  button:active{
    -webkit-transform: translateY(1px);
            transform: translateY(1px);
    transition: all 0.05s;
  }

input{
  width: 100%;
  max-width: 350px;
  border: 1px solid rgb(225 225 225);
  border: 1px solid var(--lightgray);
  height: 35px;
  border-radius: 3px;
  padding: 0 15px;
  background-color: #f3f3f3;
}
.Card{
    width: 100%;
    position: relative;
    max-width: 350px;
    height: 150px;
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 10px;
    overflow: hidden!important;
    cursor: pointer!important;
    box-shadow: 1px 2px 3px 3px rgba(0, 0, 0, 0.075);
    background-color: rgba(255, 255, 255, 0.1);  
    -webkit-filter: grayscale(60%);
    filter: grayscale(60%);
    padding: 15px;
    color:white;
    transition: all 0.1s;
    display: flex;
    align-items: center;
    justify-self: center;
}

    .Card > div{
        width: 100%;
    }

    .Card::before{
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        top:0;bottom: 0;right: 0;left:0;
        background-color: rgba(0, 0, 0, 0.2);
        backdrop-filter: blur(2px);
        -webkit-backdrop-filter: blur(2px);
        z-index: -1;
    }

    .Card:hover{
        -webkit-transform: translateY(-2px);
                transform: translateY(-2px);
        box-shadow: 1px 2px 7px 5px rgba(0, 0, 0, 0.06);
        -webkit-filter: none !important; 
        filter: none !important;
        transition: all 0.1s;
    }

    .Card:hover span:first-child{
        top: 14px;
        transition: all 0.4s;
    }

    .Card:active{
        -webkit-transform: translateY(1px);
                transform: translateY(1px);
        box-shadow: 1px 2px 3px 3px rgba(0, 0, 0, 0.075);
        -webkit-filter: none !important; 
        filter: none !important;
        transition: all 0.05s;
    }

    
    .Card *{
        cursor: pointer!important;
    }

    .Card span:first-child{
        border-radius: 50%;
        height: 30px;
        width: 30px;
        top: 15px;
        left: 40px;
        -webkit-filter: blur(6px);
                filter: blur(6px);
        opacity: 0.3;
        position: absolute;
        background-color: var(--white);
        transition: all 0.4s;
    } 

    .Card span:nth-child(2){
        border-radius: 50%;
        height: 10px;
        width: 10px;
        right: 40px;
        bottom: 30px;
        -webkit-filter: blur(2px);
                filter: blur(2px);
        opacity: 0.4;
        position: absolute;
        background-color: var(--white);
        transition: all 0.2s;
    }

    .Card img{
        position: absolute;
        top: -20px;
        left: -40px;
        width: 500px;
        height: auto;
        z-index: -2;
    }

    .Card h4{
        text-align: center;
    }

    .Card p{    
        height: auto;
        max-height: 90px;
        overflow: hidden;
        margin-top: 5px!important;
        font-size: 11pt;
        z-index: 10;
    }

#card_holder{
    display: grid;
    padding-top: 20px!important;
    grid-column-gap: 4%;
    grid-row-gap: 4%;
    grid-template-columns: repeat(4, minmax(0, 1fr));
    margin: auto!important;
    justify-content: space-evenly;
    width: 100%;
}


/* transition */
.Fade-enter {
    opacity: 0.01;
  }
  
  .Fade-enter.Fade-enter-active {
    opacity: 1;
    transition: opacity 0.2s ease-in;
  }
  
  .Fade-leave {
    opacity: 1;
  }
  
  .Fade-leave.Fade-leave-active {
    opacity: 0.01;
    transition: opacity 0.2s ease-out;
  }
#detail_view{
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.7);
    top:0;left: 0;right: 0;bottom: 0;
    z-index: 999;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px;
    cursor: pointer;
}

    #detail_view > div{
        color: white;
        background-color: transparent;
        border-radius: 10px;
        border: 1px solid white;
        padding: 20px 30px;
        width: 100%;
        max-width: 1200px;
        height: 600px;
        overflow: hidden;
        z-index: -3;
        position: relative;
    }

    #detail_view > div::before{
        background-color: rgba(255, 255, 255, 0.04);
        -webkit-backdrop-filter: blur(8px) saturate(100%) contrast(45%) brightness(130%);
                backdrop-filter: blur(8px) saturate(100%) contrast(45%) brightness(130%);
        content: '';
        height: 100%;
        position: absolute;
        width: 100%;
        z-index: -1;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
    }

    #detail_view > div > *{
        z-index: 5;
    }

    #detail_view p{
        width: 100%;
        max-width: 1000px;
        margin: auto!important;
        margin-top: 10px!important;
    }

    #detail_view h1{
        text-align: center;
        margin-top: 20px!important;
        font-size: 40pt;
    }

    #detail_view a{
        color: white;
    }

    #detail_view img{
        position: absolute;
        top: -40px;
        bottom: 0;
        left: -100px;
        right: 0;
        height: 110%;
        width: auto;
        z-index: -2!important;
        opacity: 0.3;
    }
section{
    margin-top: 30px!important;
}
#login_page{
    position: absolute;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
}

    #login_page > div{
        display: block;
        text-align: center;
        width: 100%;
        max-width: 500px;
    }

form > *{
    display: block;
    margin: auto!important;
    margin-top: 10px!important;
}

h2{
    color: var(--primary-color);
}

form > *:last-child, form > *:first-child{
    margin-top: 25px!important;
}
footer{
    background-color: var(--primary-color);
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 60px;
    display: flex;
    font-size: 11pt;
    align-items: center;
    color: var(--white);
}
